<template>
  <q-page class="bg-grey-2 fullscreen auth">
    <q-toolbar class="text-seconday">
      <q-btn
        flat
        round
        dense
        icon="mdi-arrow-left"
        color="primary"
        to="/magazine"
      />
    </q-toolbar>
    <div class="text-center text-caption">
      Escaneie o QR Code: <br /><b class="caption">{{ result }}</b>
    </div>
    <q-card
      class="absolute-center"
      flat
      :style="$q.screen.name == 'xs' ? 'min-width: 352px' : 'min-width: 480px'"
    >
      <q-card-section class="q-pa-none">
        <qrcode-stream :camera="camera" @decode="onDecode" @init="onInit">
          <div v-show="showScanConfirmation" class="light-dimmed	 fullscreen">
            <!-- <img
              :src="require('../assets/check-mark-success.gif')"
              height="100"
            /> -->
            <q-icon name="mdi-check" color="positive"  class="absolute-center" size="80px" />
          </div>
        </qrcode-stream>
      </q-card-section>
    </q-card>
    <div class="text-center text-caption absolute-bottom q-pb-xl">
      Aguarde até que o escaneamento seja feito!
    </div>
  </q-page>
</template>

<script>
import { QrcodeStream } from "vue-qrcode-reader";
export default {
  components: { QrcodeStream },
  data() {
    return {
      camera: "auto",
      result: null,
      showScanConfirmation: false,
    };
  },
  methods: {
    async onInit(promise) {
      try {
        await promise;
      } catch (e) {
        console.error(e);
      } finally {
        this.showScanConfirmation = this.camera === "off";
      }
    },

    async onDecode(content) {
      this.result = content;

      this.pause();
      await this.timeout(500);
      this.unpause();
    },

    unpause() {
      this.camera = "auto";
    },
    pause() {
      this.camera = "off";
    },

    timeout(ms) {
      return new Promise((resolve) => {
        window.setTimeout(resolve, ms);
      });
    },
  },
};
</script>

<style>
.scan-confirmation {
  position: absolute;
  width: 100%;
  height: 100%;

  background-color: rgba(255, 255, 255, 0.8);

  display: flex;
  flex-flow: row nowrap;
  justify-content: center;
}
</style>
